import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ProductsService {
  private getAllProductsUrl = "https://jsonplaceholder.typicode.com/users";

  constructor(private http:HttpClient) { }

  getAllProducts(){
    //change the link to the api's url
   // let data = this.http.get<any>("https://jsonplaceholder.typicode.com/users")
    
    return this.http.get<any>(this.getAllProductsUrl);
  }
}
